<template>
  <div id="app">
    <b-navbar toggleable="md" type="dark" class="site-header">

      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand to="/">Mindlogger</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <b-navbar-nav>
          <b-nav-item to="/activitySets" exact v-if="isLoggedIn">Your Activity Sets</b-nav-item>
        </b-navbar-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">

          <b-nav-item-dropdown right v-if="isLoggedIn">
            <!-- Using button-content slot -->
            <template slot="button-content">
              <em>{{user.login}}</em>
            </template>
            <b-dropdown-item href="#">Profile</b-dropdown-item>
            <b-dropdown-item @click="logout">Signout</b-dropdown-item>
          </b-nav-item-dropdown>

          <b-nav-item v-else to="/login">Login</b-nav-item>

        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
    <div class="">
      <transition name="fade" mode="out-in">
        <!-- <keep-alive :exclude="['view_activity']"> -->
        <router-view v-on:login="saveToken" :isLoggedIn="isLoggedIn"
        :user="user" :authToken="authToken" keep/>
        <!-- </keep-alive> -->
     </transition>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import _ from 'lodash';
// eslint-disable-next-line
import style from './custom-bootstrap.scss';
// eslint-disable-next-line
import 'bootstrap/dist/css/bootstrap.css';
// eslint-disable-next-line
import 'bootstrap-vue/dist/bootstrap-vue.css';
import '../node_modules/@fortawesome/fontawesome-free/css/all.min.css';

console.log('style', style);
const localStorage = require('localStorage');

Vue.use(BootstrapVue);

export default {
  name: 'App',
  data() {
    return {
      user: {},
      authToken: {},
    };
  },
  computed: {
    isLoggedIn() {
      return !_.isEmpty(this.user);
    },
  },
  mounted() {
    try {
      this.user = JSON.parse(localStorage.getItem('user')) || {};
      this.authToken = JSON.parse(localStorage.getItem('authToken')) || {};
    } catch (error) {
      this.user = {};
      this.authToken = {};
    }
  },
  methods: {
    saveToken(token) {
      // console.log(token);
      this.user = token.user;
      this.authToken = token.authToken;
      localStorage.setItem('user', JSON.stringify(token.user));
      localStorage.setItem('authToken', JSON.stringify(token.authToken));
    },
    logout() {
      this.user = {};
      this.authToken = {};
      localStorage.setItem('user', {});
      localStorage.setItem('authToken', {});
    },
  },
};
</script>

<style lang="scss">
  @import './custom-bootstrap.scss';
  @import '../node_modules/bootstrap/scss/bootstrap.scss';
</style>

<style>
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,200,400');

.site-header {
  background: url(//27c2s3mdcxk2qzutg1z8oa91-wpengine.netdna-ssl.com/wp-content/themes/childmind/assets/img/bg-header.svg) bottom center #0067a0;
}

#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  font-family: 'IBM Plex Sans', 'Avenir', Helvetica, Arial, sans-serif;
  /* font-weight: 100; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h1, h2, h3 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>



